@mixin ab($t, $l, $b, $r) {
    position: absolute;    
    @if $l != n { left: $l; }
    @if $t != n { top: $t; }
    @if $r != n { right: $r; }
    @if $b != n { bottom: $b; }
}
@mixin left($l) {
  left: $l;
}
@mixin right($r) {
  right: $r;
}
@mixin text-right {
    text-align: end;
}
@mixin text-left {
    text-align: start;
}

@mixin flex-end {
    justify-content: flex-end;
}
@mixin flex-start {
    justify-content: flex-start;
}
@mixin border-inline-start($value) {
    border-inline-start:$value;
}  
@mixin border-inline-end($value) {
    border-inline-end:$value;
}
@mixin padding-start($value) {
    padding-inline-start: $value;
}
@mixin padding-end($value) {
    padding-inline-end: $value;
}
@mixin margin-start($value) {
    margin-inline-start: $value;
}
@mixin margin-end($value) {
    margin-inline-end: $value;
}

.templateProduct {
	.main-product-gallery {
		margin-bottom: 35px;
		.main-slider:not(.tns-carousel) + .slider-controls, .thumbnail-slider:not(.tns-carousel) + .slider-controls  {
			display: none;
		}
	}
	.main-gallery {
		position: relative;
		margin-bottom: 10px;
		border-radius: var(--border-radius);
	}
	.thumbnail-gallery {
		position: relative;
		.thumbnail-slider-list {
			margin: 0 -5px;
			>div {
				padding: 0 5px !important;
			}
		}
		.media-item {
			>* {
				border: 2px solid transparent;
				border-radius: var(--border-radius);
				transition: 0.2s all ease-in-out;
			}
			&.tns-nav-active, &:hover {
				>* {
					border-color: var(--primary-color);
				}
			}
		}
		.slider-controls button {
			width: 30px;
			height: 30px;
			margin-top: -15px;
			svg {
				width: 6px;
			}
		}
	}
	.thumbnail-left {
		.thumbnail-gallery {
			@media (min-width: 1300px) {
				.thumbnail-slider:not(.tns-carousel) {
					>div {
						padding: 0;
						margin-bottom: 20px;
					}
				}
			}
		}
	}
	.main-product-gallery {		
		.slider-controls {
			.slider-prev-button {
				@include left(10px);
			}
			.slider-next-button {
				@include right(10px);
			}
		}
	}
	.field-countdown {		
		countdown-block {
			display: flex;
			text-align: center;
			margin: 25px 0;
			>span {
				@include margin-end(5px);
				min-width: 65px;
				font-size: 28px;
				padding: 11px 5px;
				background: var(--primary-color);
				color: var(--miscellaneous-color);
				border-radius: var(--border-radius);
				line-height: 1.1em;
				&:after {
					display: block;
					font-size: 10px;
					line-height: 1.4em;
				}
				&:before {
					display: none;
				}
			}
		}
	}
	.section-featured-collections {
		.tns-nav {
			position: relative;
			padding: 10px 0 20px;
		}
		.featured-collections--list {
			product-card {
				flex-direction: unset;
				align-items: center;
				padding-bottom: 20px;
				position: relative;
				header {
					flex: 0 0 40%;
					max-width: 100%;
					@include padding-end(15px);
				}
				main {
					flex: 1;
					padding: 0;
				}
				.main-card {
					justify-content: center;
				}
				.card-group {
					margin-top: 10px;
				}
				&:before {
					content: "";
					@include ab(n,0,0,n);
					width: 100%;
					background: var(--miscellaneous-color);
					height: 1px;
				}
			}
			.card {
				&:last-child {
					product-card {
						&:before {
							display: none;
						}
					}
				}
			}
			.card-group {
				align-items: flex-end;
				form {
					display: flex;
					flex-wrap: wrap;
					align-items: center;
				}
			}
			.quantity-inner {
				width: 88px;
				background: #F0F0F0;
				height: 100%;
				position: relative;
				cursor: pointer;
				border-radius: var(--border-radius);
				select {
					min-height: 36px;
					height: 100%;
					border: 0;
					background: none;
					@include padding-start(15px);
				}
				svg {
					@include ab(0,n,n,15px);
					height: 100%;
					path {
						fill: var(--text-color);
					}
				}
			}
			.card {
				margin-bottom: 20px;
			}
			.product-card-form {
				width: auto;
			}
			.quantity-select {
				background: none;
				@include margin-end(10px);
				margin-bottom: 10px;
			}
			.product-weight-unit {
				@include margin-end(5px);
				margin-bottom: 10px;
				min-width: 30px;
				display: block;
			}
			.btn {
				max-width: 185px;
				min-width: 130px;
				.text {
					margin-top: 1px;
				}
			}
		}
	}
	@media (min-width: 750px) {
		.section-featured-collections {
			.featured-collections--list {
				.card {
					&:nth-child(odd) {
						product-card {
							&:after {
								content: "";
								@include ab(0,n,n,0);
								width: 1px;
								height: calc(100% - 20px);
								background: var(--miscellaneous-color);
							}
						}
					}
					&:nth-child(even) {
						product-card:before {
							width: calc(100% + 20px);
							@include ab(n,-20px,n,n);
						}
					}
					&:nth-last-child(2) {
						product-card {
							&:before {
								display: none;
							}
						}
					}
				}
				product-card {
					header {
						@include padding-end(20px);
					}
				}
			}
		}
	}
	@media (min-width: 1300px) {
		.section-featured-collections {
			.main-collection.flex {
				product-card header {
					flex: 0 0 30%;
				}
			}
			.featured-collections--list {
				.product-card-form { 
					max-width: 100%;
				}
				.quantity-select, .product-weight-unit {
					margin-bottom: 0;
				}
				product-card {
					header {
						flex: 0 0 210px;
					}
				}
				.btn {
					min-width: 180px;
					flex: 1;
				}
			}
		}
		.thumbnail-left {
			.gallery-wrapper {
				display: flex;
			}
			.main-gallery {
				order: 2;
				flex: 1;
			}
			.thumbnail-gallery {
				order: 1;
				flex: 0 0 75px;
				max-width: 75px;
				@include margin-end(12px);
				.slider-controls {
					button {
						transform: rotate(90deg);
					}
				}
				.slider-prev-button {
					top: 0;
					@include left(50%);
					@include margin-start(-15px);
				}
				.slider-next-button {
					bottom: 0;
					top: auto;
					@include right(50%);
					@include margin-end(-15px);
				}
			}
			.media-item {
				width: 100%;
				display: block;
			}
			.thumbnail-slider {
				margin: 0;
				>div {
					padding: 0;
				}
			}
		}
	}
}
.main-product-info {
	position: relative;
	.product-vendor {
		font-size: 13px;
		line-height: 1.4em;
		display: flex;
		align-items: center;
		color: var(--subtext-color);
		flex: 1;
	}
	.product-title {
		font-size: 35px;
		font-weight: 500;
		line-height: 1.2em;
		margin: 8px 0 13px;
	}
	.label {
		padding: 4px 10px;
		font-size: 11px;
		line-height: 1.47em;
		border-radius: var(--border-radius);
		display: block;
		font-weight: 500;
		min-width: 50px;
		text-align: center;
	}
	.product-logo {
		border-radius: var(--border-radius);
		border: 1px solid var(--miscellaneous-color);
		@include margin-end(10px);
	}
	.product-price {
		margin: 0 0 25px;
		.flex {
			align-items: center;
		}
		.price {
			font-size: 36px;
			line-height: 1.12em;
			font-weight: 600;
		}
		.price-compare {
			font-size: 26px;
			line-height: 1.12em;
			@include margin-end(7px);
		}
	}
	.price-sold-out {
		color: var(--primary-color);
		font-weight: 500;
		font-size: 26px;
	}
	.product-reviews-badge {
		.spr-badge {
			margin: 10px 0 15px;
		}
		.spr-badge-caption {
			font-size: 13px;
		}
	}
	.product-reviews-badge {
		.spr-starrating {
			@include margin-end(10px);
		}
		span[data-rating="0.0"] {
			display: none;
		}
	}
	.field-text {
		margin-bottom: 10px;
	}
	.field-info {
		margin: 0 0 10px;
		>.flex {
			align-items: center;
		}
		.field-title {
			font-size: 14px;
			font-weight: 600;
			color: var(--subtext-color);
			@include margin-end(10px);
		}
		&:not(.has-wlcp-text) {
			.dynamic-btn {
				order: 5;
			}
		}
	}
	.field-content {
		li {
			@include margin-end(5px);
		}
		a {
			color: var(--subtext-color);
			font-size: 14px;
			line-height: 1.4em;
			text-transform: capitalize;
			transition: 0.2s all ease-in-out;
			&:hover {
				color: var(--primary-color);
			}
		}
	}
	.product-options {
		margin: 0;
		.variants-wrapper {
			display: flex;
			flex-wrap: wrap;
			margin: 0 -10px;
		}
	}
	.option-type-select {
		padding: 0 10px;
		flex: 1;
		margin-bottom: 25px;
		label {
			font-size: 14px;
			line-height: 1.5em;
			color: var(--subtext-color);
		}
		.select-wrapper {
			border: 1px solid var(--miscellaneous-color);
			border-radius: var(--border-radius);
			background: var(--background-color);
			height: 40px;
			display: flex;
			align-items: center;
			position: relative;
			select {
				cursor: pointer;
				color: var(--subtext-color);
				font-size: 13px;
				font-weight: 600;
				height: 100%;
				padding: 0 15px;
				border-radius: var(--border-radius);
				border: 0;
			}
			svg {
				@include ab(n,n,n,15px);
				pointer-events: none;
				path {
					fill: var(--subtext-color);
				}
			}
		}
	}
	.variant-options {
		padding: 0 10px;
		width: 100%;
		margin-bottom: 25px;
		.swt-header {
			p {
				margin: 0 0 5px;
				font-size: 14px;
				line-height: 1.5em;
				color: var(--subtext-color);
			}
		}
	}
	.swt-container {
		margin: -5px;
	}
	.swt-type-color {
		label.c-white {
			background: #fff;
		}
	}
	.swt-type-color, .swt-type-image {
		padding: 5px;
		position: relative;
		label {
			width: 40px;
			height: 40px;
			border: 1px solid var(--miscellaneous-color);
			margin: 0;
			border-radius: var(--border-radius);
			overflow: hidden;
		}
		&:hover {
			label {
				border-color: var(--primary-color);
			}
			input.disabled + label {
				border-color: var(--miscellaneous-color);
			}
		}
		input:checked + label {
			border: 2px solid var(--primary-color);
		}
		input.disabled + label {
			cursor: not-allowed;
			opacity: 0.67;
			&:before, &:after {
				content: "";
				width: 90%;
				height: 1px;
				display: flex;
				align-items: center;
				justify-content: center;
				position: relative;
				background: var(--miscellaneous-color);
			}
			&:before {
				top: 51%;
				left: 2.5px;
				transform: rotate(45deg);
			}
			&:after {
				top: 49%;
				right: -2px;
				transform: rotate(-45deg);
			}
		}
		.tooltip {
			@include ab(-25px,50%,n,n);
			min-width: 60px;
			transform: translateX(-50%);
			text-align: center;
			background: var(--background-color);
			border: 1px solid var(--primary-color);
			border-radius: var(--border-radius);
			z-index: 1;
			color: var(--primary-color);
			height: 24px;
			font-size: 13px;
			line-height: 22px;
			opacity: 0;
			visibility: hidden;
			transition: 0.2s all ease-in-out;
			&:before {
				content: "";
				@include ab(n,50%,-5px,n);
				width: 8px;
				height: 8px;
				@include margin-start(-5px);
				transform: rotate(45deg);
				background: var(--background-color);
				z-index: -1;
				border-bottom: 1px solid var(--primary-color);
				@include border-inline-end(1px solid var(--primary-color));
			}
		}
		&:hover {
			.tooltip {
				opacity: 1;
				visibility: visible;
			}
		}
	}
	.swt-type-button {
		padding: 5px;
		label {
			height: 26px;
			font-size: 13px;
			font-weight: 600;
			border: 1px solid var(--miscellaneous-color);
			border-radius: var(--border-radius);
			display: flex;
			align-items: center;
			justify-content: center;
			margin: 0;
			padding: 0 18px;
			transition: 0.2s all ease-in-out;
			position: relative;
			color: var(--subtext-color);
			background: var(--background-color);
		}
		&:hover {
			label {
				border: 1px solid var(--primary-color);
			}
		}
		input:checked + label:before {
			content: "";
			@include ab(-1px,-1px,-1px,-1px);
			border: 2px solid var(--primary-color);
			border-radius: var(--border-radius);
		}
		input.disabled + label {
			cursor: not-allowed;
			opacity: 0.67;
			text-decoration: line-through;
			&:hover {
				border: 1px solid var(--miscellaneous-color);
			}
		}
	}
	.stock-count {
		margin: 0;
		color: var(--subtext-color);
		font-size: 14px;
		.stock {
			font-weight: 600;
			color: #f00000;
			display: inline-block;
			@include margin-start(5px);
		}
	}
	.field-sharing {
		margin: 25px 0;
		display: flex;
	}
	.social-sharing {
		display: none;
		flex-wrap: wrap;
		margin: 0 -4px;
		@include padding-end(8px);
		>li {
			padding: 0 4px;
			transition: 0.2s all ease-out;
			&:hover {
				a {
					opacity: 0.7;
				}
			}
		}
		a {
			padding: 0;
			display: flex;
			align-items: center;
			justify-content: center;
			width: 32px;
			height: 32px;
			border-radius: var(--border-radius);
			svg {
				width: 12px;
				height: 12px;
				path {
					fill: #fff;
				}
			}
		}
		&.active {
			display: flex;
		}
	}
	.share-list-btn {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 32px;
		height: 32px;
		background: #95d03a;
		border-radius: var(--border-radius);
		cursor: pointer;
		transition: 0.2s all ease-out;
		&:hover {
			opacity: 0.7;
		}
		svg {
			width: 12px;
			height: 12px;
			path {
				fill: #fff;
			}
		}
	}
	.share-facebook {
		background: #4267b2;
	}
	.share-twitter {
		background: #55acee;
	}
	.share-pinterest {
		background: #cb2027;
	}
	.svg-content {
		margin-top: 10px;
		.svg-content-inner {
			>div {
				margin-top: 12px;
			}
		}
		.icon {
			@include margin-end(13px);
		}
		.text {
			color: var(--subtext-color);
		}
	}
	.style-horizontal {
		.svg-content-inner >div {
			width: 100%;
		}
	}
	.field-border {
		margin: 25px 0;
		border: 0;
		border-top: 1px solid var(--miscellaneous-color);
	}
	.product-cart-inner {
		display: flex;
		flex-wrap: wrap;
		margin: -5px 0;
		>* {
			padding: 5px 0;
		}
	}
	.quantity-select {
		@include margin-end(10px);
		flex: 0 0 100%;
	}
	.quantity-inner {
		background: #F0F0F0;
		max-width: 126px;
		border-radius: var(--border-radius);
		input::-webkit-outer-spin-button,
		input::-webkit-inner-spin-button {
			appearance: none;
			margin: 0;
		}
		input {
			width: calc(100% - 80px);
			padding: 0;
			display: flex;
			align-items: center;
			justify-content: center;
			text-align: center;
			font-size: 15px;
			height: 46px;
			border: 0;
		}
		button {
			padding: 10px;
			width: 40px;
			height: 46px;
			display: flex;
			align-items: center;
			justify-content: center;
			.icon-minus {
				width: 7px;
				height: 7px;
			}
		}
	}
	.product-form {
		display: block;
		margin-bottom: 25px;
	}
	.product-cart_button {
		flex: 1;
		button {
			height: 46px;
			width: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			&[disabled] {
				opacity: 0.67;
				cursor: not-allowed;
			}
		}
		svg {
			display: flex;
			@include margin-end(13px);
		}
		.icon-loading {
			width: 20px;
			height: 20px;
			display: none;
			path {
				fill: #fff;
			}
		}
		.loading {
			.icon-loading {
				display: flex;
			}
			span.icon {
				display: none;
			}
		}
	}
	.wlcp-in-text {
		flex: 0 0 100%;
		margin-top: 10px;
	}
	.wlcp-target {
		display: flex;
		padding: 5px 0;
		>* {
			color: var(--primary-color);
			text-decoration: underline;
			text-underline-offset: 2px;
			transition: 0.2s all ease-in-out;
			z-index: auto;
			@include margin-start(10px);
			&:hover {
				color: var(--text-color);
			}
		}
		.btn-wishlist svg {
			width: 16px;
			height: 16px;
		}
		.btn-show-text {
			margin: 0;
			display: flex;
			align-items: center;
		}
		.btn-wishlist.btn-show-text {
			@include padding-end(20px);
		}
		.arn_added {
			color: var(--secondary-color);
		}
	}
	.btn-show-icon {
		transition: 0.2s all ease-in-out;
		>div {
			width: 46px;
			height: 46px;
			border: 1px solid var(--miscellaneous-color);
			border-radius: var(--border-radius);
			display: flex;
			align-items: center;
			justify-content: center;
		}
		&.arn_added {
			>div {
				border-color: var(--primary-color);
			}
			&.btn-compare svg path {
				fill: var(--primary-color);
			}
			&.btn-wishlist svg {
				path {
					stroke: var(--primary-color);
				}
			}
		}
		&:hover {
			&.btn-compare svg path {
				fill: var(--primary-color);
			}
			&.btn-wishlist svg {
				path {
					stroke: var(--primary-color);
				}
			}
			>div {
				border-color: var(--primary-color);
			}
		}
	}
	.product-cart {
		display: flex;
		flex-wrap: wrap;
		.product-cart-inner {
			width: 100%;
		}
		.dynamic-btn {
			width: 100%;
		}
	}
	@media (min-width: 750px) {
		margin-bottom: 35px;
		.style-horizontal {
			.svg-content-inner>div {
				width: auto;
				&:not(:last-child) {
					@include padding-end(40px);
				}
			}
		}
	}
	@media (min-width: 1000px) {
		.quantity-select {
			flex: unset;
		}
		.product-cart_button {
			max-width: 305px;
		}
	}
	@media (min-width: 1200px) {
		.option-type-select {
			max-width: 50%;
		}
		.product-cart_button {
			button {
				max-width: 305px;
			}
		}
	}
}
.store-availability-wrapper {
	margin-bottom: 20px;
	.closest-store-title {
		display: flex;
		align-items: center;
		margin: 12px 0;
		>svg {
			width: 18px;
			height: 18px;
			@include margin-end(5px);
		}
		strong {
			@include margin-start(5px);
		}
	}
	.closest-store-title-pickup-time {
		margin: 12px 0;
	}
	.store-availabilities-product-title {
		font-size: 20px;
		margin-bottom: 12px;
	}
	.store-item-location-name {
		font-size: 18px;
		margin-bottom: 8px;
	}
	.store-item-pick-time {
		margin-bottom: 15px;
	}
	.store-item-address {
		p {
			margin: 0;
			line-height: 1.6em;
		}
	}
	button.link {
		font-size: 15px;
		text-underline-offset: 1px;
		transition: 0.25s all ease-in-out;
		&:hover {
			color: var(--primary-color);
		}
	}
}
.preorder-poup {
	.popup-inner {
		max-width: 440px;
		width: 90vw;
	}
	.popup-heading {
		text-transform: uppercase;
		font-size: 20px;
		margin-bottom: 20px;
	}
	.form-field {
		margin-bottom: 18px;
		input, textarea {
			padding: 5px 15px;
			border-radius: var(--border-radius);
		}
		textarea {
			height: 115px;
		}
	}
	.form-button-group {
		button {
			min-width: 200px;
		}
	}
} 
.field-promo {
	margin: 25px 0;
	.inherit-color {
		padding: 3.5px 10px;
		text-align: center;
		justify-content: center;
		border: 1px dashed currentColor;
		border-radius: 5px;
		font-weight: 500;
		font-size: var(--body-fs);
		>div {
			justify-content: center;
		}
		span.icon {
			@include margin-end(13px);
			display: flex;
		}
	}
} 
.variant-soldout {
	.shopify-payment-button {
		pointer-events: none;
	}
	.product-cart_button .btn {
		pointer-events: none;
	}
	.dynamic-btn, .product-cart_button {
		cursor: not-allowed;
		opacity: 0.67;
	}
}

.product-layout-default {
	@media (min-width: 750px) {
		.main-product-gallery {
			flex: 0 0 50%;
		}
		.main-product-info {
			flex: 0 0 50%;
		}
	}
	@media (min-width: 1000px) {
		.main-product-gallery {
			flex: 0 0 45%;
		}
		.main-product-info {
			flex: 0 0 55%;
		}
	}
	@media (min-width: 1200px) {
		.product-cart {
			.product-cart-inner {
				flex: 1;
			}
		}
	}
}

.product-layout-column {
	.product-info {
		margin-bottom: 40px;
	}
	.first-column {
		flex: 1;
	}
	.product-info-inner {
		>div:last-child {
			margin-bottom: 0;
		}
	}
	.second-column {
		.product-info-inner {
			width: 100%;
			background: var(--product-form-bg-color);
			padding: 30px;
			border-radius: var(--border-radius);
		}
		.product-form {
			margin-bottom: 0;
		}
		.wlcp-in-text {
			flex-wrap: wrap;
			margin: -5px 0;
			>a {
				padding: 5px 0;
			}
		}
	}
	.has-wlcp-icon {
		.wlcp-in-icon {
			flex: 0 0 100%;
			>a:first-child {
				margin: 0;
			}
			a {
				flex: 1;
				div {
					width: 100%;
				}
			}
		}
	}
	.product-cart-inner {
		flex-wrap: wrap;
		.quantity-select {
			margin-bottom: 10px;
			.quantity-inner {
				background: var(--background-color);
			}
		}
		.product-cart_button {
			width: 100%;
			margin-bottom: 10px;
		}
	}
	.main-product-info .option-type-select {
		flex: 0 0 100%;
		max-width: 100%;
	}
	@media (min-width: 750px) {
		.main-product-gallery {
			flex: 0 0 50%;
		}
		.main-product-info {
			flex: 0 0 50%;
			product-form {
				flex-wrap: wrap;
			}
		}
	}
	@media (min-width: 1000px) {
		.main-product-gallery {
			flex: 0 0 50%;
		}
		.main-product-info {
			flex: 0 0 50%;
		}
	}
	@media (min-width: 1200px) {
		.main-product-gallery {
			flex: 0 0 41%;
		}
		.main-product-info {
			flex: 0 0 59%;
		}
		.second-column {
			.product-info-inner {
				max-width: 295px;
			}
		}
	}
}
#shopify-product-reviews {
	.spr-review-content {
		p,span {
			font-size: inherit;
		}
	}
	.spr-container {
		border: 1px solid var(--miscellaneous-color);
		.spr-header-title {
			font-size: 24px;
		}
		.spr-summary-actions-newreview {
			color: var(--primary-color);
			transition: 0.25s all ease-in-out;
			&:hover {
				opacity: 0.75;
			}
		}
	}
	.spr-header .spr-starrating .spr-icon {
		font-size: 95%;
	}
	.spr-icon {
		width: 13px;
		height: 13px;
		min-width: auto;
		min-height: auto;
		top: 0;
		font-size: 14px;
		@include margin-end(3px);
		&:before {
			font-size: inherit;
			width: 13px;
			height: 13px;
		}
	}
	.spr-starrating {
		margin: 0;
		margin-top: 10px;
		input {
			display: none;
		}
	}
}
